import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import DatePicker from '../../date-picker';
import Typography from '../../typography';

const DatePickerExample = () => {
  const [startDate, setStartDate] = useState(new Date());

  return (
    <Grid>
      <Row>
        <Col>
          <Row>
            <Col>
              <Typography tag="h2">Date Picker</Typography>
              <Typography>Based on React Datepicker 2.10.1 see more examples at <a href="https://reactdatepicker.com/">https://reactdatepicker.com/</a></Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <DatePicker
                id="test-datepicker-input"
                onChange={date => setStartDate(date)}
                selected={startDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </Grid>
  );
}
export default DatePickerExample;
