import {
  bool,
  node,
  object,
  string
} from "prop-types";
import React from "react";

export default function CalendarContainer({
  className,
  children,
  showPopperArrow,
  arrowProps = {}
}) {
  return (
    <div className={className}>
      {showPopperArrow && (
        <div className="react-datepicker__triangle" {...arrowProps} />
      )}
      {children}
    </div>
  );
}

CalendarContainer.propTypes = {
  className: string,
  children: node,
  arrowProps: object, // react-popper arrow props
  showPopperArrow: bool
};
